.App {
  text-align: center;
}

body,
html {
  height: 100%;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  background: #e0e0e0;
}

.wrapper {
  min-height: 100%;
  height: auto !important;
  height: 100%;
  margin: 0 auto -82px;
  padding: 0px 40px;
}

.push {
  height: 82px;
}

h4 {
  font-style: italic;
  margin-top: 10px !important;
}

li {
  margin-top: 15px !important;
  list-style-type: none;
}

a {
  text-decoration: none !important;
}

